/**
 * 
 * @param fileUrl
 * @param savePath
 * @param successCallback
 * @param errorCallback
 * @returns {Promise<any>}
 */
export function downloadFileClear(fileUrl, savePath, successCallback, errorCallback) {
    if (!window.Arcgis || typeof window.Arcgis.downloadFileClear !== 'function') {
        return Promise.reject('')
    }

    window.Arcgis.downloadFileClear(fileUrl, savePath, successCallback, errorCallback)
}

/**
 * 
 * @returns {Promise<any>}
 */
export function getDeviceBaseFilePath() {
    if (!window.cordova) {
        return Promise.reject('cordova')
    }
    const filePath = window.cordova.file.externalRootDirectory
    return filePath.toString().replace("file://", "")
}

/**
 * 
 * @returns {Promise<any>}
 */
export function getSavePath() {
    return getDeviceBaseFilePath() + 'gdhdt/map/'
}



