import axios from '../common/js/axios'

/***
 * 
 * @param params
 * @param params.page  // 
 * @param params.pageSize  // 
 * @returns {AxiosPromise<any>}
 */
export function getSearchList(params) {
    const url = `/search`

    return axios.get(url, {params})
}

/***
 *  FID 
 * @param fid
 * @return {AxiosPromise<any>}
 */
export function queryTypeDictByFid(fid) {
    const url = `/public/typeDict/queryTypeDictByFid`

    return axios.post(url, {fid})
}

export function getParams(key) {
    const url = `/public/getSysParam`

    return axios.post(url, {paramKey: key})
}



