export const search = {
    water(minX, minY, maxX, maxY) {
        // if (!Store.getters['layer/layers'][0].status) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }

        getMapWater(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, () => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    boat(minX, minY, maxX, maxY) {
        // if (!Store.getters['layer/layers'][1].status) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }

        getMapBoat(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    mark(minX, minY, maxX, maxY) {
        // if (!Store.getters['layer/layers'][2].status) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }

        getMapMark(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    weather(minX, minY, maxX, maxY, level) {
        // if (!Store.getters['layer/layers'][3].status) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY,
            level
        }

        getMapWeather(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    report(minX, minY, maxX, maxY) {
        // if (!Store.getters['layer/layers'][5].status && !Store.getters['user/isLogin']) return
        if (!Store.getters['user/isLogin']) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }

        getReport(data).then(list => {
            console.log("list.length:",list)
            if(list.length==0){
                window.Arcgis.emptyLayer("013019",function(msg){console.log(msg)})
                return
            }
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    label(minX, minY, maxX, maxY) {
        // if (!Store.getters['layer/layers'][5].status && !Store.getters['user/isLogin']) return
        if (!Store.getters['user/isLogin']) return

        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }

        getMapLabel(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c. ', 'color: #2196f3')
            }, err => {
                console.error(err, '. ')
            })
        })
    },
    depth(minX, minY, maxX, maxY) {
        if (!Store.getters['map/depth']) return

        const params = {
            dpv: window.$depth,
            minx: minX,
            miny: minY,
            maxx: maxX,
            maxy: maxY
        }

        getDepth(params).then(res => {
            window.Arcgis && window.Arcgis.setAirworthiness([res])
        })
    }
}

/***
 * 
 */
export function routerOfflineMap() {
    Router.push({name: 'offline-map'})
}



