export const brightness = {
    // 
    set(value) {
        return new Promise((resolve, reject) => {
            const brightness = window.cordova ? window.cordova.plugins.brightness : null

            if (!brightness) {
                return reject('  ')
            }

            brightness.setBrightness(value, resolve, reject)
        })
    },
// 
    get() {
        return new Promise((resolve, reject) => {
            const brightness = window.cordova ? window.cordova.plugins.brightness : null

            if (!brightness) {
                return reject('  ')
            }

            brightness.getBrightness(resolve, reject)
        })
    },
// 
    setScreenOn(visible = false) {
        const brightness = window.cordova ? window.cordova.plugins.brightness : null

        if (!brightness) {
            return console.log('  ')
        }

        return brightness.setKeepScreenOn(visible)
    }
}

export const app = {
    // 
    async version() {
        const app = window.cordova ? window.cordova.getAppVersion : null

        if (!app) {
            return Promise.resolve('0.0.0')
        }

        return await app.getVersionNumber()
    },
    // 
    name() {
        const app = window.cordova ? window.cordova.getAppVersion : null

        if (!app) {
            return Promise.reject('  ')
        }

        return app.getAppName()
    },
    // 
    packageName() {
        const app = window.cordova ? window.cordova.getAppVersion : null

        if (!app) {
            return Promise.reject('  ')
        }

        return app.getPackageName()
    },
    // 
    versionCode() {
        const app = window.cordova ? window.cordova.getAppVersion : null

        if (!app) {
            return Promise.reject('  ')
        }

        return app.getVersionCode()
    }
}

window.$cordova_api = {brightness, app}

export default window.$cordova_api



