import axios from '../common/js/axios'
import {json} from "../common/js/axios"

/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapBoat(data) {
    const url = `/channel/map/boat`

    return axios.post(url, data)
}

/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapWater(data) {
    const url = `/channel/map/waterstation`

    return axios.post(url, data)
}


/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapMark(data) {
    const url = `/channel/map/mark`

    return axios.post(url, data)
}


/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapMap(data) {
    const url = `/channel/map/map`

    return axios.post(url, data)
}


/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapWeather(data) {
    const url = `/channel/map/weather`

    return axios.post(url, data)
}


/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getMapLabel(data) {
    const url = `/personal/queryLabelByCoordinate`

    data.minLongitude = data.min_longitude
    data.maxLongitude = data.max_longitude
    data.minLatitude = data.min_latitude
    data.maxLatitude = data.max_latitude


    return axios.post(url, data)
}


/***
 * 
 * @param params
 * @param params.dpv     
 * @param params.maxx    
 * @param params.maxy    
 * @param params.minx    
 * @param params.miny    
 * @return *
 */
export function getDepth(params) {
    const url = `/depth/area`

    return axios.get(url, {params}).then(res => {
        try {
            res = JSON.parse(res)
        } catch (error) {
            console.log(error)
        }

        return Promise.resolve(res)
    })
}


/***
 * 
 * @param data
 * @param data.centerPoint            
 * @param data.centerPoint.latitude   
 * @param data.centerPoint.longitude  
 * @param data.name                   
 * @param data.types{<Array>}         
 * @returns {AxiosPromise<any>}
 */
export function getRouteTips(data) {
    const url = `/route/tips`

    return json.post(url, data)
}

/***
 * 
 * @param data
 * @param data.min_longitude  // 
 * @param data.max_longitude  // 
 * @param data.min_latitude   // 
 * @param data.max_latitude   // 
 * @returns {AxiosPromise<any>}
 */
export function getReport(data) {
    const url = `/personal/queryReportByCoordinate`

    data.minLongitude = data.min_longitude
    data.maxLongitude = data.max_longitude
    data.minLatitude = data.min_latitude
    data.maxLatitude = data.max_latitude
    data.status="032001"
    return axios.post(url, data)
}



